"use client";

import { useState } from "react";

export default function Field({ domain, pass }) {
  const [visible, setVisible] = useState(false);
  return (
    <div className="flex flex-row">
      <label className="w-1/2">{domain}</label>
      <div className="w-1/2">
        <input type={visible ? "text" : "password"} value={pass} readOnly />
        <button
          onClick={() => {
            setVisible(!visible);
          }}
        >
          v
        </button>
      </div>
    </div>
  );
}
